/* utili */
.main-inner {
  padding: 64px 0 80px; }

/* section  - header */
.main-header {
  padding: 30px 88px;
  background-color: #AA0601;
  position: relative; }

.main-header logo {
  width: 80px;
  height: 49px; }

.main-header logo img {
  width: 100%;
  vertical-align: middle; }

.main-header .main-nav {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  color: #fff; }

.main-header .main-nav li {
  font-size: 20px;
  line-height: 30px;
  margin-right: 64px; }

.main-header .main-nav a {
  width: 100%;
  text-decoration: none;
  position: relative;
  color: #ffffff; }

.main-header .main-nav a:after {
  content: "";
  position: absolute;
  left: 50%;
  right: 50%;
  bottom: -5px;
  height: 0;
  border-bottom: 1px solid #fff;
  transition: .3s; }

.main-header .main-nav a:hover:after {
  left: 0;
  right: 0; }

.main-header .main-nav li:nth-of-type(4) {
  margin-right: 0px; }

/* .m-hamburger{
    display: none;
} */
/* section  - banner */
.banner {
  background-image: url(https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/week3/banner.png);
  height: 840px; }

.banner-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  text-align: right;
  height: 100%;
  font-weight: bold; }

.banner-text {
  font-size: 36px; }

/* section - glassSeries */
.glass-series-list li {
  display: flex;
  margin-bottom: 30px; }

.glass-series-list li img {
  max-width: 60%; }

.glass-series-list h2 {
  color: #AA0601;
  text-transform: uppercase;
  font-style: italic;
  font-weight: bolder;
  padding: 0px 24px;
  margin-bottom: 0; }

/* section - about */
.about-list li {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 23.8%;
  padding-right: 24px; }

.about-list li:last-child {
  margin-right: 0; }

.about-list .item-img {
  width: 100px;
  height: 100px;
  margin-bottom: 40px; }

.glass-about p {
  padding: 0px 37px; }

/* section - glassCustomer */
.customer-list li {
  border: 1px solid #ccc;
  width: 23.8%;
  margin-bottom: 1.5rem;
  margin-right: 1.5rem; }

.customer-list-head img {
  width: 100%; }

.customer-list-body {
  padding: 16px 16px; }

.customer-list-body p {
  padding-bottom: 16px; }

.customer-list-body p ~ p + p {
  color: #707070;
  padding-bottom: 0px; }

.customer-list-body p:first-child {
  font-weight: bold; }

@media (max-width: 1024px) {
  .customer-list li {
    margin-right: 0px; } }

/* section - glass-contact */
.glass-contact p {
  font-size: 20px;
  margin-bottom: 20px; }

.glass-contact .submit {
  margin-bottom: 12px; }

/* footer */
footer {
  background-color: #AA0601;
  padding: 40px 0px;
  color: #fff; }

.mb-big {
  margin-bottom: 32px; }

.mb-medium {
  margin-bottom: 24px; }

.mb-small {
  margin-bottom: 22px; }

/* footer-nav-menu */
