  justify-content: space-between;
  border-bottom: 1px solid #fff; }

.footer-nav-menu {
  display: flex;
  margin-bottom: 38px; }

.footer-nav-menu li {
  margin-right: 48px; }

.footer-nav-menu li a {
  width: 100%;
  color: #ffffff;
  text-decoration: none; }

.footer-nav-menu li:last-child {
  margin-right: 0; }

/* footer-info-contact */
