.footer-info-contact li img {
  margin-right: 22px; }

.footer-info-contact li:nth-of-type(2) img {
  margin-right: 12px; }

.footer-info-contact p {
  font-size: 36px; }

/* medaia-area */
.medaia-area {
  display: flex; }

.medaia-area li {
  margin-right: 32px; }

.medaia-area li:last-child {
  margin-right: 0; }

/* footer-copyright */
.footer-copyright-list {
  font-size: 14px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center; }

.footer-copyright-list li img {
  margin-right: 38px; }

.footer-copyright-list li a {
  margin-right: 62px; }

.footer-copyright-list li a:last-child {
